@import "../../inc/vars";
.acro-trustpilot-widget {
	padding: 32px 0;
	@media(min-width: $screen-sm) {
		padding: 60px 0;
	}
	.acro-trustpilot-widget__wrapper {
		text-align: center;
		h2 {
			padding: 0 28px;
			@include pop(400, 35, 1.24);
			background: var(--grad-title, linear-gradient(180deg, #2D3442 0%, #6C7689 100%));
			background-clip: text;
			-webkit-background-clip: text;
			-webkit-text-fill-color: transparent;
			margin-bottom: 16px;
			@media(min-width: $screen-sm) {
				margin-bottom: 8px;
			}
		}
		h5 {
			padding: 0 28px;
			@include pop(400, 18, 1.24);
			margin-bottom: 32px;
			color: #5A6272;
			@media(min-width: $screen-sm) {
				@include pop(400, 24, 1.24);
				margin-bottom: 16px;
			}
		}
		.trustpilot-widget {
			padding: 20px 28px;
			@media(min-width: $screen-sm) {
				padding: 20px 40px;
			}
			iframe {
				width: 100% !important;
			}
		}
	}
}

.page-template-page-ppc-landing {
	.acro-trustpilot-widget {
		.acro-trustpilot-widget__wrapper {
			h2 {
				@include titleGrad();
				@media(min-width: $screen-sm) {
					font-size: 44px;
				}
			}
		}
	}

}